import Vue from 'vue'
import Tips from './src/tips'

// 
const TipsConstructor = Vue.extend(Tips)

/**
 * @param { Object } option 
 * title: xxx
 * message: xxx
 */
export default function tips (option) {
  // 1. 
  const instance = new TipsConstructor({
    //  data 
    data: option
  })

  // 2. 
  //  dom 
  instance.$mount()
  //  body 
  // $el  dom 
  //  Notice.vue  div
  document.body.appendChild(instance.$el)

  // 
  return instance
}
