<template>
  <div class="tyh-input">
    <input
      class="tyh-input_inp"
      :class="[sizeClass]"
      :type="inpType"
      :value="value"
      :placeholder="innerText"
      @input="handleInput"
    />
  </div>
</template>

<script>
export default {
  name: 'TyhInput',
  components: {},
  props: {
    //  v-model  value 
    value: [String, Number],

    // 
    innerText: String,

    // 
    inpType: {
      type: String,
      default: 'text',
      validator: function (value) {
        // 
        return ['text', 'password'].indexOf(value) !== -1
      }
    },

    // 
    size: {
      type: String,
      default: 'medium'
    }
  },
  data () {
    return {}
  },
  computed: {
    //  class
    sizeClass () {
      return this.size ? 'tyh-input_inp--' + this.size : 'tyh-input_inp--medium'
    }
  },
  watch: {},
  created () { },
  mounted () { },
  methods: {
    //  input   
    handleInput (evt) {
      // inputinputemit
      this.$emit('input', evt.target.value)
    }
  }
}
</script>

<style src="../../../style/input/index.css" scoped></style>
