<template>
  <div class="tyh-list">
    <!--  -->
    <div v-if="header" class="tyh-list-header">
      {{ header }}
    </div>

    <!--  -->
    <div v-if="content.length" class="tyh-list-content">
      <ul class="tyh-list-content-ul">
        <li
          class="tyh-list-content-li"
          :class="[zebra ? 'tyh-list-content-li-zebra' : '']"
          v-for="(item, index) in content"
          :key="index"
        >
          <!--  -->
          <span class="tyh-list-content-li-num" v-if="num">
            {{ index + 1 }}
          </span>
          <!--  -->
          {{ contentKey(item) }}
        </li>
      </ul>
    </div>

    <!--    -->
    <div v-else class="tyh-list-content">
      <p class="note">...</p>
    </div>

    <!--  -->
    <div v-if="footer" class="tyh-list-footer">
      {{ footer }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'TyhList',
  components: {},
  props: {
    // header 
    header: {
      type: [String, Number],
      require: false
    },

    // 
    content: {
      type: [Array, String],
      require: false
    },

    // 
    iskey: {
      type: String,
      require: false
    },

    // footer 
    footer: {
      type: [String, Number],
      require: false
    },

    // 
    zebra: {
      type: Boolean,
      default: false
    },

    // 
    num: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {}
  },
  computed: {},
  watch: {},
  created () { },
  mounted () { },
  methods: {
    //  
    contentKey (item) {
      //  item  key 
      if (typeof item === 'object' && !this.iskey) {
        return item
      }
      //  item 
      if (typeof item !== 'object') {
        return item
      }
      //  
      for (const key in item) {
        if (this.iskey === key) {
          return item[key]
        }
      }
    }
  }
}
</script>

<style src="../../../style/list/index.css" scoped></style>
