<template>
  <button
    class="tyh-button"
    :class="[typeClass, roundClass, prohibitClass]"
    @click="onClick"
    @mousedown="onMousedown"
    @mouseup="onMouseup"
  >
    <span class="tyh-button-text">
      <slot></slot>
    </span>
  </button>
</template>

<script>
export default {
  name: 'TyhButton',
  components: {},
  props: {
    // 
    type: {
      type: String,
      default: ''
    },

    // 
    round: Boolean,

    // 
    prohibit: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {}
  },
  computed: {
    //  class
    typeClass () {
      return this.type ? 'tyh-button--' + this.type : 'tyh-button--'
    },
    //  class
    roundClass () {
      return this.round ? 'tyh-button--round' : ''
    },
    //  class
    prohibitClass () {
      return this.prohibit ? 'tyh-button--prohibit--' + this.type : ''
    }
  },
  watch: {},
  created () { },
  mounted () { },
  methods: {
    // 
    onClick (evt) {
      if (this.prohibit) {
        return
      }
      this.$emit('click', evt)
    },
    // 
    onMousedown (evt) {
      if (this.Ban) {
        return
      }
      this.$emit('mousedown', evt)
    },
    // 
    onMouseup (evt) {
      if (this.Ban) {
        return
      }
      this.$emit('mouseup', evt)
    }
  }
}
</script>

