<template>
  <div
    v-if="isShow"
    class="tyh-tips"
    :class="[positionClass]"
    :style="[backgroundColorStyle]"
  >
    <!--  -->
    <div class="tyh-tips-header">
      <h4 class="tyh-tips-title">{{ title }}</h4>

      <!--  -->
      <span v-if="showClose" class="tyh-tips-close" @click="closeTips">
        
      </span>
    </div>

    <!--  -->
    <p v-if="isHtml" v-html="message"></p>
    <p v-else class="tyh-tips-title-message">{{ message }}</p>
  </div>
</template>

<script>
export default {
  name: 'TyhTips',
  components: {},
  props: {},
  data () {
    return {
      title: '', // 
      message: '', // 
      times: null, // 
      isShow: false, // 
      position: null, // 
      showClose: true, // 
      isHtml: false, // message  html 
      backgroundColor: '#fff' // 
    }
  },
  computed: {
    // 
    positionClass () {
      return this.position
        ? 'tyh-tips-' + this.position
        : 'tyh-tips-top-right'
    },
    // 
    backgroundColorStyle () {
      return { background: this.backgroundColor }
    }
  },
  watch: {},
  created () { },
  mounted () {
    // 
    this.isShow = true
    //  times  3500 
    if (this.times === null) {
      setTimeout(() => {
        this.isShow = false
      }, 3500)
      return
    }
    // 
    setTimeout(() => {
      this.isShow = false
    }, this.times)
  },
  methods: {
    // 
    closeTips () {
      this.isShow = false
    }
  }
}
</script>

<style src="../../../style/tips/index.css" scoped></style>
